<template>
<div class="card shadow product-card">
  <div class="row">
    <div class="col-5">
      <img class="img" :src="require('../assets/' + prop_product.image)" alt="">
    </div>
    <div class="col-7">
	<div class="like" @click="onLike"><i class="fa fa-heart" :class="[prop_product.love ? heartActive : heartNoActive]"></i></div>
      <div class="card-body">
        <p class="card-title">{{prop_product.title}}</p>
        <p class="card-text">{{prop_product.description}}</p>
        <div class="row">
	<div class="price col-6">{{prop_product.price}} руб.</div>
	<div v-if="quantityProduct < 1" class="basket col-6" @click="onAddInBasket">В КОРЗИНУ</div>
	<div v-if="quantityProduct > 0" class="col-6">
	<div class="row">
	<div @click="onDelFromBasket" class="col-4 q-min-max"><i class="fa fa-minus"></i></div><div class="col-4 quantity">{{quantityProduct}}</div><div @click="onAddInBasket" class="col-4 q-min-max"><i class="fa fa-plus"></i></div>
	</div>
	</div>
	</div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
name: 'ProductCardBig',
data(){
return{
heartActive:'heart-active',
heartNoActive:'heart-no-active'
}
},
props:{
  prop_product: {
  type: Object,
  default: function() {
  return {}
  }
  }
},
methods:{
onLike(){
this.$store.dispatch("addLike", this.prop_product);
},
onAddInBasket(){
this.$store.dispatch("addInBasket", this.prop_product);
},
onDelFromBasket(){
this.$store.dispatch("delFromBasket", this.prop_product);
}
},
computed:{
quantityProduct(){
var inBasketPC = this.$store.getters.basket;
var quantityInBasketPC = 0;
for(var e = 0; e < inBasketPC.length; e++){
if(inBasketPC[e].title == this.prop_product.title){
quantityInBasketPC = inBasketPC[e].quantity; 
}
}
return quantityInBasketPC;
}
}
}
</script>


<style scoped>
.product-card{
border:0px;
border-radius:10px;
width:100%;
margin-left:auto;
margin-right:auto;
margin-top:10px;
margin-bottom:10px;
}
.like{
position:absolute;
top:7px;
right:22px;
width:30px;
height:30px;
border-radius:15px;
background-color:#fff;
padding-top:5px;
cursor:pointer;
}
.heart-active{
color:#e59e1f;
}
.heart-no-active{
color:#D2D2D2;
}
.img{
width:250px;
height:100%;
border-top-left-radius:10px;
border-bottom-left-radius:10px;
}
.card-title{
font-weight:800;
text-align:left;
}
.card-text{
font-size:14px;
text-align:left;
}
.price{
text-align:left;
color:#e59e1f;
font-weight:600;
}
.basket{
text-align:right;
font-weight:600;
cursor:pointer;
}
.q-min-max{
height:30px;
border-radius:15px;
background-color:#e59e1f;
color:#fff;
cursor:pointer;
line-height:31px;
}
.quantity{
color:#e59e1f;
line-height:31px;
}
</style>