<template>
<div class="container category">

<div @click="isOnCategory = !isOnCategory" class="row border-line p-3" :class="[isOnCategory ? categoryActive : '']">
<div class="left-category col-10" :style="{ color: prop_color}" :class="[isOnCategory ? textWhite : '']">{{prop_category.title}}</div>
<div class="right-category col-2" :style="{ color: prop_color}" :class="[isOnCategory ? textWhite : '']">
<i v-if="!isOnCategory" class="fa fa-angle-right fa-lg"></i>
<i v-if="isOnCategory" class="fa fa-angle-down fa-lg"></i>
</div>
</div>

<div v-if="isOnCategory" class="row products">
<div class="col-12">

<div v-if="window.width < 590">
<ProductCard v-for="product in prop_category.products" :key="product.title" :prop_product="product"/>
</div>
<div v-if="window.width > 590">
<ProductCardBig v-for="product in prop_category.products" :key="product.title" :prop_product="product"/>
</div>

</div>
</div>

</div>
</template>

<script>
import ProductCard from '../components/ProductCard.vue'
import ProductCardBig from '../components/ProductCardBig.vue'
export default {
name: 'Category',
components: {
    ProductCard,
    ProductCardBig
  },
data(){
  return{
  isOnCategory:false,
  categoryActive:'category-active',
  textWhite:'text-white',
  window: {
            width: 0,
            height: 0
        }
  }
},
props:{
  prop_category: {
  type: Object,
  default: function () {
  return { title:''}
  }
  },
  prop_color: {
  type: String,
  default: function () {
  return '#000'
  }
  },
},
created() {
        window.addEventListener('resize', this.handleResize);
        this.handleResize();
    },
    destroyed() {
        window.removeEventListener('resize', this.handleResize);
    },
    methods: {
        handleResize() {
            this.window.width = window.innerWidth;
            this.window.height = window.innerHeight;
        }
    }
}
</script>


<style scoped>
.category{
background-color:#fff;
margin-top:-1px;
}
.products{
background-color:#F8F8F8;
}
.left-category{
text-align:left;
font-weight:600;
font-size:15px;
}
.right-category{
text-align:right;
}
.border-line{
border-top: 1px solid #F2F2F2;
border-bottom: 1px solid #F2F2F2;
}
.category-active{
background-color:#e59e1f;
}
.text-white{
color:#fff;
}
</style>
