<template>
<div class="main">

<IconBar class="fixed-top" @oninfo="onInfo"/>

<MainAffiche/>
<div class="mt-3">
<Category :prop_category = "{title:'МЫ РЕКОМЕНДУЕМ', products:recommended}" prop_color = "#e59e1f"/>
<Category :prop_category = "{title:'ПОПУЛЯРНОЕ', products:popular}" prop_color = "#e59e1f"/>
<Category v-if="like.length" :prop_category = "{title:'ИЗБРАННОЕ', products:like}" prop_color = "#e59e1f"/>
</div>

<div class="mt-3">
<Category v-for="category in categories" :key="category.title" :prop_category = "category"/>
</div>
<transition name="bounce">
<div v-if="$store.getters.basket.length > 0" @click="onBasket" class="basket-button fixed-bottom mb-3">{{productsInBasket}}</div>
</transition>
<transition name="bounce">
<Information v-if="info" @oninfo="onInfo"/>
</transition>
</div>
</template>

<script>
import IconBar from '../components/IconBar.vue'
import MainAffiche from '../components/MainAffiche.vue'
import Category from '../components/Category.vue'
import Information from '../components/Information.vue'
//import * as db from '../assets/db.js'
export default {
  name: 'Main',
  components: {
    IconBar,
	MainAffiche,
	Category,
	Information
  },
  data(){
  return{
  //categories:db.categories,
  //recommended:db.recommended,
  //popular:db.popular,
  info:false
  }
  },
  computed:{
  categories(){
  return this.$store.getters.categories;
  },
  recommended(){
  return this.$store.getters.recommended;
  },
  popular(){
  return this.$store.getters.popular;
  },
  like(){
  return this.$store.getters.like;
  },
  productsInBasket(){
  var inBasketM = this.$store.getters.basket;
  var quantityInBasketM = 0;
  var priceProBasket = 0;
  for(var i = 0; i < inBasketM.length; i++){
  quantityInBasketM = quantityInBasketM + inBasketM[i].quantity;
  priceProBasket = priceProBasket + inBasketM[i].quantity * inBasketM[i].price;
  }
  return `В КОРЗИНЕ ${quantityInBasketM} ТОВАР НА ${priceProBasket} Р`;
  }
  },
  methods:{
  onInfo(){
  this.info = !this.info;
  },
  onBasket(){
  this.$router.push('/basket');
  }
  }
}
</script>


<style scoped>
.main {
max-width: 600px;
margin-left: auto;
margin-right: auto;
background-color:#F8F8F8;
}
.basket-button{
/*
position:fixed;
bottom:20px;
left:0;
right:0;
*/
width:290px;
height:50px;
background-color:#e59e1f;
color:#fff;
font-weight:600;
font-size:12px;
border-radius:25px;
margin-left:auto;
margin-right:auto;
text-align: center;
line-height:50px;
cursor:pointer;
}
.bounce-enter-active {
  animation: bounce-in .5s;
}
.bounce-leave-active {
  animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}
</style>
