import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Main.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'main',
    component: Home
  },
  {
    path: "/basket",
    name: "basket",
    component: () => import("../views/Basket.vue")
  },
]

const router = new VueRouter({
  mode: "history",
  routes
})

export default router
