import Vue from 'vue'
import Vuex from 'vuex'
import * as db from '../assets/db.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
	categories:db.categories,
    recommended:db.recommended,
    popular:db.popular,
    like:[],
	basket:[]
  },
  ///////////////////////////////////////////////////////////
  mutations: {
    setLike(state, payload) {
	if(payload.status == "category"){
	for(var i = 0; i < state.categories.length; i++){
		for(var ii = 0; ii < state.categories[i].products.length; ii++){
			if(state.categories[i].products[ii].title === payload.title){
				state.categories[i].products[ii].love = !state.categories[i].products[ii].love;
			}
		}
	}
	}else if(payload.status == "recommended"){
	for(var r = 0; r < state.recommended.length; r++){
			if(state.recommended[r].title === payload.title){
				state.recommended[r].love = !state.recommended[r].love;
			}
	}
	}else if(payload.status == "popular"){
	for(var p = 0; p < state.popular.length; p++){
			if(state.popular[p].title === payload.title){
				state.popular[p].love = !state.popular[p].love;
			}
	}
	}
	state.like = [];
	//state.like.length = 0;
	for(var a = 0; a < state.categories.length; a++){
		for(var aa = 0; aa < state.categories[a].products.length; aa++){
			if(state.categories[a].products[aa].love){
				state.like.push(state.categories[a].products[aa]);
			}
		}
	}
	for(var b = 0; b < state.recommended.length; b++){
			if(state.recommended[b].love){
				state.like.push(state.recommended[b]);
			}
	}
	for(var c = 0; c < state.popular.length; c++){
			if(state.popular[c].love){
				state.like.push(state.popular[c]);
			}
	}
    },
	addBasket(state, payload) {
	if(state.basket.length < 1){
		state.basket.push(payload);
	}else{
		var isProductInBasket = false;
		for(var k = 0; k < state.basket.length; k++){
			if(state.basket[k].title == payload.title){
				state.basket[k].quantity++;
				isProductInBasket = true;
			}
		}
		if(!isProductInBasket){
			state.basket.push(payload);
		}
	}
    },
	delBasket(state, payload) {
	for(var d = 0; d < state.basket.length; d++){
			if(state.basket[d].title == payload.title){
				if(state.basket[d].quantity > 1){
					state.basket[d].quantity--;
				}else{
					state.basket.splice(d, 1);
				}
			}
		}
    },
	delAllBasket(state, payload) {
	if(payload){
		state.basket = [];
	}
    },
  },
  //////////////////////////////////////////////////////////
  actions: {
    addLike({ commit }, product) {
        commit("setLike", product);
    },
	addInBasket({ commit }, basket) {
        commit("addBasket", basket);
    },
	delFromBasket({ commit }, basket) {
        commit("delBasket", basket);
    },
	delAllFromBasket({ commit }) {
        commit("delAllBasket", true);
    },
  },
  //////////////////////////////////////////////////////////
  getters: {
	categories: (state) => {
      return state.categories;
    }, 
	recommended: (state) => {
      return state.recommended;
    },
	popular: (state) => {
      return state.popular;
    },
    like: (state) => {
      return state.like;
    },
	basket: (state) => {
      return state.basket;
    },
  }
});